*{font-family:Quantico;}html{min-height:100%;position:relative;}.contenedor-titulo{margin-top:2.5rem;text-align:center;font-size:2rem;}.contenedor-titulo h2{font-size:4rem;margin-bottom:2rem;}.contenedor-imagen{display:flex;align-items:center;}.contenedor-imagen img{width:100%;}#titulo{position:absolute;z-index:100;width:100%;}.formulario{width:50%;padding:16px;border-radius:10px;margin:auto;background-color:#222222;margin-bottom:5rem;}.formulario label{color:white;}.boton{border-radius:1rem;padding:2%;background-color:rgb(255,255,255);transition:all .5s;}.boton:hover{color:black;background-color:gold;transition:all .5s;}textarea{width:100%;height:100px;border:1px solid #f6f6f6;border-radius:3px;background-color:var(--);margin:8px 0;resize:none;display:block;}h1{text-align:center;font-size:4rem;color:white;}@media (max-width:480px){.contenedor-imagen{width:100%}.contenedor-imagen h1{font-size:1.5rem}.contenedor-titulo h2{margin-top:1.5rem;text-align:center;font-size:1.2rem}.formulario{width:80%;margin-bottom:10rem}.formulario label{font-size:1.5rem}}@media (min-width:481px) and (max-width:779px){.contenedor-imagen{width:100%}.contenedor-imagen img{width:100%;height:400px}.contenedor-imagen h1{font-size:2rem}.contenedor-titulo h2{margin-top:1.5rem;text-align:center;font-size:1.8rem}.formulario{margin-bottom:12rem}.formulario label{font-size:1.5rem}}@media (min-width:780px){.contenedor-imagen{width:100%}.contenedor-imagen img{width:100%;height:400px}.contenedor-imagen h1{font-size:2.5rem}.contenedor-titulo h2{margin-top:1.8rem;text-align:center;font-size:2rem}.formulario{margin-bottom:12rem}}@media (min-width:1200px){.contenedor-imagen{width:100%}.contenedor-imagen h1{font-size:3rem}.contenedor-titulo h2{margin-top:1.8rem;text-align:center;font-size:2.5rem}}@media (min-width:2000px){.contenedor-imagen{width:100%}.contenedor-imagen h1{font-size:4rem}.contenedor-titulo h2{margin-top:2rem;text-align:center;font-size:3.1rem}}