* {
  font-family: Quantico;
}

html {
  min-height: 100%;
  position: relative;
}

.contenedor-titulo {
  text-align: center;
  margin-top: 2.5rem;
  font-size: 2rem;
}

.contenedor-titulo h2 {
  margin-bottom: 2rem;
  font-size: 4rem;
}

.contenedor-imagen {
  align-items: center;
  display: flex;
}

.contenedor-imagen img {
  width: 100%;
}

#titulo {
  z-index: 100;
  width: 100%;
  position: absolute;
}

.formulario {
  width: 50%;
  background-color: #222;
  border-radius: 10px;
  margin: auto auto 5rem;
  padding: 16px;
}

.formulario label {
  color: #fff;
}

.boton {
  background-color: #fff;
  border-radius: 1rem;
  padding: 2%;
  transition: all .5s;
}

.boton:hover {
  color: #000;
  background-color: gold;
  transition: all .5s;
}

textarea {
  width: 100%;
  height: 100px;
  background-color: var(--);
  resize: none;
  border: 1px solid #f6f6f6;
  border-radius: 3px;
  margin: 8px 0;
  display: block;
}

h1 {
  text-align: center;
  color: #fff;
  font-size: 4rem;
}

@media (max-width: 480px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen h1 {
    font-size: 1.5rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.5rem;
    font-size: 1.2rem;
  }

  .formulario {
    width: 80%;
    margin-bottom: 10rem;
  }

  .formulario label {
    font-size: 1.5rem;
  }
}

@media (min-width: 481px) and (max-width: 779px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen img {
    width: 100%;
    height: 400px;
  }

  .contenedor-imagen h1 {
    font-size: 2rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.5rem;
    font-size: 1.8rem;
  }

  .formulario {
    margin-bottom: 12rem;
  }

  .formulario label {
    font-size: 1.5rem;
  }
}

@media (min-width: 780px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen img {
    width: 100%;
    height: 400px;
  }

  .contenedor-imagen h1 {
    font-size: 2.5rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.8rem;
    font-size: 2rem;
  }

  .formulario {
    margin-bottom: 12rem;
  }
}

@media (min-width: 1200px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen h1 {
    font-size: 3rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.8rem;
    font-size: 2.5rem;
  }
}

@media (min-width: 2000px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen h1 {
    font-size: 4rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 2rem;
    font-size: 3.1rem;
  }
}

